.main-header {
  width: 100%;
  font-size: 0.9em;
}

.main-header .top-header {
  text-align: right;
}

.main-header .top-header .contact-info {
  display: inline-block;
}

.main-header .top-header .lang-switcher {
  color: #fff;
  display: none;
}

.main-header .top-header a {
  display: inline-block;
  padding: 10px 15px;
  color: #fff;
  text-decoration: none;
}

.main-header .top-header a i.fa {
  margin-right: 5px;
  color: #ffaf00;
}

.main-header .navi-bar {
  position: relative;
}

.main-menu {
  list-style: none;
  padding-left: 0;
  text-align: right;
  margin: 0;
}

.main-menu > li {
  display: inline-block;
}

.main-menu > li.current:after, .main-menu > li:after {
  content: "";
  margin: 0 auto;
  display: block;
  width: 0;
  height: 3px;
  background: #ffaf00;
  -webkit-transition: all .3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.main-menu > li:hover:after {
  width: 100%;
}

.main-menu > li > a {
  display: block;
  text-decoration: none;
  color: #fff;
  letter-spacing: 1px;
  padding: 5px 15px;
}

.logos {
  float: left;
  display: inline-block;
}

.logos svg {
  width: 250px;
}

.logos svg .st0 {
  fill: #fff;
}

.logos svg .st1 {
  fill: #F0AB00;
}

.main-header.header-scroll {
  position: fixed;
  z-index: 99999;
}

.main-header.header-scroll .top-header {
  display: none;
}

.main-header.header-scroll .bottom-header {
  background: #fff;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.3);
}

.main-header.header-scroll .bottom-header .logos svg {
  width: 200px;
  margin-top: 5px;
}

.main-header.header-scroll .bottom-header .logos svg .st0 {
  fill: #a6a6a6;
}

.main-header.header-scroll .main-menu > li > a {
  color: #333;
  padding: 15px;
}

.nav-buttons {
  display: none;
  list-style: none;
}

@media screen and (max-width: 1199px) {
  .main-header .main-menu > li > a {
    padding: 15px;
  }
  .main-header .main-menu > li > a:hover {
    background: rgba(15, 170, 255, 0.1);
  }
  .main-header .top-header {
    text-align: left;
  }
  .nav-buttons {
    display: inline-block;
  }
  #primary-nav {
    visibility: hidden;
    -webkit-transition: visibility 0s 0.3s;
    transition: visibility 0s 0.3s;
    position: absolute;
    right: 0;
    width: 300px;
    background: #fff;
    box-shadow: 0 2px 3px rgba(0, 0, 0, 0.2);
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    z-index: 1;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-transform: translateX(0);
    transform: translateX(0);
    -webkit-transition: -webkit-transform 0.3s;
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
  }
  #primary-nav.nav-is-visible {
    border-top: 1px solid #eaeaea;
    visibility: visible;
    -webkit-transition: visibility 0s 0s;
    transition: visibility 0s 0s;
  }
  #primary-nav ul {
    position: absolute;
    right: 0;
