}

.program .program-list > li:last-child:after {
  content: "";
  display: inline-block;
  width: 14px;
  height: 14px;
  border: 2px solid #fafafa;
  background: #0faaff;
  position: absolute;
  border-radius: 50%;
  left: -48px;
  bottom: 0;
